import React, { Component } from 'react';
import h from 'react-hyperscript';
import logo from './logo.svg';
import ReactJson from 'react-json-view';
import './App.scss';
class App extends Component {
    componentDidMount() {
        let self = this;
        this.indexI = this.indexJ = 0;
        this.initWebsocket();
        this.renderTree();
        this.selectText(this.preCode);
    }
    addClass() {
        var pres = document.getElementsByClassName('pre');
        //这个其实是执行forEach方法，只不过那个数组的this变成了pres而已，可以看成换了数组元素
        //由于forEach原本接受的参数就是函数，所以这里第二个参数可以是函数。
        [].forEach.call(pres, function(item, i) {
            item.classList.add('prettyprint', 'linenums');
        });
        window.prettyPrint();
    }
    initWebsocket() {
        let self = this;
        let socket = new WebSocket('ws://localhost:3000/');
        socket.onopen = () => {
            socket.send('getOriginData');
        };
        socket.onmessage = msg => {
            let data = msg.data;
            data = JSON.parse(data);
            console.log('originData', data, JSON.parse);
            if (data.name === 'originData') {
                self.__DATA__ = JSON.parse(data.data);
                self.oldVariable = self.__DATA__[0][0].oldVariable;
                self.newVariable = self.__DATA__[0][0].newVariable;
                self.args = self.__DATA__[0][0].args;
                self.preCode.textContent = self.__DATA__[0][0].func.replace(/function __GETVAR__\(name\) {return eval\(name\);} var __VAR__ = SAVE\(arguments, __GETVAR__\)\;/g, '');
                this.setState({
                    a: 1
                });
                this.preCode.className = 'pre';
                console.log('=====', self.__DATA__, self.my_json_object);
                self.addClass();
            }
        };
    }

    nextStep() {
        if (this.indexI < this.__DATA__[this.indexJ].length) {
            this.indexI++;
        }
        else {
            this.indexJ++;
            this.indexI = 0;
        }
        this.preCode.textContent = this.__DATA__[this.indexJ][this.indexI].func.replace(/function __GETVAR__\(name\) {return eval\(name\);} var __VAR__ = SAVE\(arguments, __GETVAR__\)\;/g, '');
        this.oldVariable = this.__DATA__[this.indexJ][this.indexI].oldVariable;
        this.newVariable = this.__DATA__[this.indexJ][this.indexI].newVariable;
        this.args = this.__DATA__[this.indexJ][this.indexI].args;
        this.setState({
            a: 1
        });
        this.preCode.className = 'pre';
        this.addClass();
    }
    prevStep() {
        if (this.indexI > 0) {
            this.indexI--;
        }
        else {
            if (this.indexJ > 0) {
                this.indexJ--;
                this.indexI = this.__DATA__[this.indexJ] && this.__DATA__[this.indexJ].length || 0;
            }
        }
        this.preCode.textContent = this.__DATA__[this.indexJ][this.indexI].func.replace(/function __GETVAR__\(name\) {return eval\(name\);} var __VAR__ = SAVE\(arguments, __GETVAR__\)\;/g, '');
        this.oldVariable = this.__DATA__[this.indexJ][this.indexI].oldVariable;
        this.newVariable = this.__DATA__[this.indexJ][this.indexI].newVariable;
        this.args = this.__DATA__[this.indexJ][this.indexI].args;
        this.setState({
            a: 2
        });

        this.preCode.className = 'pre';
        console.log(this.indexJ, this.indexJ);

        this.addClass();
    }
    selectText(element) {
        let txt = window.getSelection
                ? window.getSelection().toString()
                : document.selection.createRange().text;
        return txt;
    }
    renderTree() {
        let me = this;
        var d3 = window.d3;
        var margin = {top: 30, right: 20, bottom: 30, left: 20},
            width = 960,
            barHeight = 20,
            barWidth = (width - margin.left - margin.right) * 0.2;
        var i = 0,
            duration = 400,
            root;
        var diagonal = d3.linkHorizontal()
            .x(function(d) { return d.y; })
            .y(function(d) { return d.x; });
        var svg = d3.select(".tree-box").append("svg")
            .attr("width", width) // + margin.left + margin.right)
            .append("g")
            .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
        d3.json("treeData.json", function(error, flare) {
            if (error) throw error;
            root = d3.hierarchy(flare);
            root.x0 = 0;
            root.y0 = 0;
            update(root);
        });
        function update(source) {
            // Compute the flattened node list.
            var nodes = root.descendants();
            var height = Math.max(500, nodes.length * barHeight + margin.top + margin.bottom);
            d3.select("svg").transition()
                .duration(duration)
                .attr("height", height);
            // d3.select(self.frameElement).transition()
            //     .duration(duration)
            //     .style("height", height + "px");
            // Compute the "layout". TODO https://github.com/d3/d3-hierarchy/issues/67
            var index = -1;
            root.eachBefore(function(n) {
                n.x = ++index * barHeight;
                n.y = n.depth * 20;
            });
            // Update the nodes…
            var node = svg.selectAll(".node")
                .data(nodes, function(d) { return d.id || (d.id = ++i); });
            var nodeEnter = node.enter().append("g")
                .attr("class", "node")
                .attr("transform", function(d) { return "translate(" + source.y0 + "," + source.x0 + ")"; })
                .style("opacity", 0);
            // Enter any new nodes at the parent's previous position.
            nodeEnter.append("rect")
                .attr("y", -barHeight / 2)
                .attr("height", barHeight)
                .attr("width", barWidth)
                .style("fill", color)
                .on("click", click);
            nodeEnter.append("text")
                .attr("dy", 3.5)
                .attr("dx", 5.5)
                .text(function(d) { return d.data.name; });
            // Transition nodes to their new position.
            var clickEl = nodeEnter.append("rect")
                .attr("y", -barHeight/2)
                .attr("x", barWidth - 30)
                .attr("height", barHeight)
                .attr("width", '30')
                .style("fill", 'green')
                .on("click", renderSource);
            nodeEnter.append("text")
                .attr("dy", barHeight/2-10)
                .attr("dx", barWidth - 20)
                .text(function(d) { return d.data.index; });

            nodeEnter.transition()
                .duration(duration)
                .attr("transform", function(d) { return "translate(" + d.y + "," + d.x + ")"; })
                .style("opacity", 1);
            node.transition()
                .duration(duration)
                .attr("transform", function(d) { return "translate(" + d.y + "," + d.x + ")"; })
                .style("opacity", 1)
                .select("rect")
                .style("fill", color);
            // Transition exiting nodes to the parent's new position.
            node.exit().transition()
                .duration(duration)
                .attr("transform", function(d) { return "translate(" + source.y + "," + source.x + ")"; })
                .style("opacity", 0)
                .remove();
            // Update the links…
            var link = svg.selectAll(".link")
                .data(root.links(), function(d) { return d.target.id; });
            // Enter any new links at the parent's previous position.
            link.enter().insert("path", "g")
                .attr("class", "link")
                .attr("d", function(d) {
                    var o = {x: source.x0, y: source.y0};
                    return diagonal({source: o, target: o});
                })
                .transition()
                .duration(duration)
                .attr("d", diagonal);
            // Transition links to their new position.
            link.transition()
                .duration(duration)
                .attr("d", diagonal);
            // Transition exiting nodes to the parent's new position.
            link.exit().transition()
                .duration(duration)
                .attr("d", function(d) {
                    var o = {x: source.x, y: source.y};
                    return diagonal({source: o, target: o});
                })
                .remove();
            // Stash the old positions for transition.
            root.each(function(d) {
                d.x0 = d.x;
                d.y0 = d.y;
            });
            function renderSource(d) {
                let index = d.data.index;
                console.log('ddddd', d, clickEl);
                clickEl._groups.forEach(item => {
                   item.forEach(list => {
                       list.__data__.isClick = false;
                   });
                });
                d.isClick = true;
                clickEl.style('fill', setClickColor);
                if (index) {
                    me.preCode.textContent = me.__DATA__[0][index].func.replace(/function __GETVAR__\(name\) {return eval\(name\);} var __VAR__ = SAVE\(arguments, __GETVAR__\)\;/g, '');
                    me.oldVariable = me.__DATA__[0][index].oldVariable;
                    me.newVariable = me.__DATA__[0][index].newVariable;
                    me.args = me.__DATA__[0][index].args;
                    me.preCode.className = 'pre';
                    me.addClass();
                    console.log(d3.select('.source-box'), d);
                    let sourceBox = d3.select('.source-box');
                    sourceBox.style("position", 'absolute');
                    sourceBox.style("left", d.y + barWidth + 100 + 'px');
                    sourceBox.style("top", d.x + 'px');
                    me.setState({
                        a: 1
                    });
                }
                update(d);
            }
        }
        // Toggle children on click.
        function click(d) {
            console.log(d);
            if (d.children) {
                d._children = d.children;
                d.children = null;
            } else {
                d.children = d._children;
                d._children = null;
            }
            update(d);
        }
        function setClickColor(d) {
            return d.isClick ? "red" : "green";
        }
        function color(d) {
            return d._children ? "#3182bd" : d.children ? "#c6dbef" : "#fd8d3c";
        }
    }
    render() {
        console.log('ss', h('div#App', 'aa').outerHTML);
        let self = this;
        console.log('00', self.my_json_object);
        return (
            h('div#App', [
                h('div.pop-code-box', {
                    ref(e) {
                        self.popCodeBox = e;
                    }
                }, [
                    h('pre', {
                        className: 'pop-code',
                        onMouseUp(e) {
                        },
                        ref(e) {
                            self.popCode = e;
                        }
                    }, '')
                ]),
                // h('div', [
                //     h('button.prev-btn', {
                //         onClick(e) {
                //             self.prevStep();
                //         }
                //     }, ['上一步']),
                //     h('button.next-btn', {
                //         ref(e) {
                //         },
                //         onClick(e) {
                //             console.log(1);
                //             self.nextStep();
                //         }
                //     }, ['下一步']),
                // ]),
                h('div.code-box', [
                    h('div.tree-box'),
                    h('div.source-box', [
                        h('div.source-code', [
                            h(ReactJson,{displayDataTypes: false, theme: 'twilight', src: {arguments: self.args}}),
                            h('pre', {
                                className: 'pre code',
                                onMouseDown() {

                                },
                                onMouseUp(e) {
                                    e.stopPropagation();
                                    let text = self.selectText(self.preCode);
                                    console.log(text);
                                    let x = e.pageX;
                                    let y = e.pageY;
                                    self.popCodeBox.setAttribute("style","left:" + (+x + 30) + 'px;'+"top:" + y + 'px;display: block');
                                    let value = self.newVariable[text] || self.oldVariable[text];
                                    if (value) {
                                        self.popCode.textContent = value;
                                    }
                                    else {
                                        try {
                                            console.log(value, eval(value));
                                            let result = eval(text);
                                            self.popCode.textContent = eval(text);
                                        }
                                        catch (e) {
                                            console.log(e);
                                        }

                                    }
                                    console.log(self.popCode.textContent);
                                    self.popCode.className = 'pre';
                                    self.addClass();
                                },
                                ref(e) {
                                    self.preCode = e;
                                }
                            }, '')
                        ]),
                        h('div.json-view', [
                            h(ReactJson,{displayDataTypes: false, theme: 'twilight', src: {oldVariable: self.oldVariable}}),
                            h(ReactJson,{displayDataTypes: false, theme: 'twilight', src: {newVariable: self.newVariable}}),
                        ])
                    ])
                ])
            ])
        );
    }
}

export default App;
